<template>
  <div id="login">
    <!-- logo -->
    <div class="login_img">
      <van-image
        round
        width="1.8rem"
        height="1.8rem"
        src="http://47.115.51.185/h5/img/logo2.47964cc3.png"
      />
    </div>
    <div class="kk">
      <!-- 框 -->
      <van-tabs v-model="active" class="lgoin_form" v-if="mode == 1">
        <!-- 账号密码 -->
        <van-tab title="账号登录">
          <van-form
            @submit="doLogin"
            show-error
            @failed="onFailed"
            :show-error-message="false"
            validate-trigger="onSubmit"
          >
            <van-field
              v-model="username"
              name="account"
              left-icon="user-o"
              placeholder="请输入账号"
              :rules="[
                { required: true, pattern: iphone, message: '请填写账号' },
              ]"
            />
            <van-field
              v-model="password"
              left-icon="lock"
              name="password"
              type="password"
              placeholder="密码"
              :rules="[
                { required: true, pattern: code, message: '请填写密码' },
              ]"
            />
            <div class="forgotYourPassword">
              <p @click="mode = 3">
                <i class="iconfont icon-wenhao"></i>忘记密码
              </p>
            </div>
            <div style="margin: 16px">
              <van-button round block type="info" native-type="submit"
                >登录</van-button
              >
            </div>
          </van-form>
        </van-tab>
        <!-- 手机号 -->
        <van-tab title="快速登录">
          <van-form
            @submit="logg"
            show-error
            @failed="onFailed"
            :show-error-message="false"
            validate-trigger="onSubmit"
          >
            <van-field
              v-model="phone"
              name="phone"
              left-icon="user-o"
              placeholder="请输入手机号"
              :rules="[
                { required: true, pattern: iphone, message: '请填写手机号' },
              ]"
            />
            <van-field
              v-model="captcha"
              left-icon="lock"
              name="captcha"
              type="text"
              placeholder="请填入验证码"
              :rules="[{ required: true, message: '请填写验证码' }]"
            >
              <template #button>
                <van-button
                  :disabled="sendFlag"
                  size="small"
                  type="primary"
                  round
                  block
                  native-type="button"
                  @click="send(phone, 1)"
                  :style="{
                    background: sendFlag
                      ? 'gray'
                      : 'linear-gradient(to bottom, #ff4b1f, #ff9068)',
                  }"
                >
                  <template>
                    <!--  -->
                    <span :class="{ zIn: sendFlag }">发送验证码</span>
                  </template>
                  <template>
                    <span :class="{ zIn2: !sendFlag }" class="btn-span"
                      >{{ num }}s</span
                    >
                  </template>
                </van-button>
              </template>
            </van-field>
            <div style="margin: 16px">
              <van-button round block type="info" native-type="submit"
                >登录</van-button
              >
            </div>
          </van-form>
        </van-tab>
      </van-tabs>

      <div class="signUp" v-if="mode != 1">
        <form>
          <h3>{{ mode == 2 ? "注册账号" : "忘记密码" }}</h3>
          <div class="sigUp_item">
            <div class="sigUp_ico iconfont icon-shouji"></div>
            <div class="sigUp_input">
              <input
                type="text"
                placeholder="输入手机号码"
                v-model="sjs"
                v-if="mode == 2"
              />
              <input
                type="text"
                placeholder="输入手机号码"
                v-model="zh_sjs"
                v-if="mode == 3"
              />
            </div>
          </div>
          <div class="sigUp_item">
            <div class="sigUp_ico iconfont icon-iconset0114"></div>
            <div class="sigUp_input">
              <input
                type="number"
                placeholder="填写验证码"
                v-model="yzs"
                v-if="mode == 2"
              />
              <input
                type="number"
                placeholder="填写验证码"
                v-model="zh_yzs"
                v-if="mode == 3"
              />
              <p
                @click="send(sjs, 2)"
                :class="yzmd ? 'acive' : ''"
                v-if="mode == 2"
              >
                <span v-if="!yzmd">获取验证码</span
                ><span v-else>{{ nums }}s</span>
              </p>
              <p
                @click="send(zh_sjs, 3)"
                :class="zh_f ? 'acive' : ''"
                v-if="mode == 3"
              >
                <span v-if="!zh_f">获取验证码</span
                ><span v-else>{{ zh_num }}s</span>
              </p>
            </div>
          </div>
          <div class="sigUp_item">
            <div class="sigUp_ico iconfont icon-mima"></div>
            <div class="sigUp_input">
              <input
                type="password"
                placeholder="请填写您的登录密码"
                v-model="mms"
                v-if="mode == 2"
              />
              <input
                type="password"
                placeholder="请填写您的登录密码"
                v-model="zh_mms"
                v-if="mode == 3"
              />
            </div>
          </div>
        </form>
        <button @click="registerNow" v-if="mode == 2">立即注册</button>
        <button v-if="mode == 3" @click="ack">确认</button>
      </div>
      <!-- 没有账号 -->
      <p class="zc" v-if="mode == 1 || mode == 2">
        {{ mode == 1 ? "没" : "已" }}有账号？<span
          v-if="mode == 1"
          @click="mode = 2"
          >立即注册</span
        ><span v-else @click="mode = 1">立即登录</span>
      </p>
      <!-- 忘记密码 -->
      <p class="zh" v-if="mode == 3">
        <span @click="mode = 1">立即登录</span>
      </p>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      list: ["账号密码登录", "手机快速登录"],
      username: "",
      password: "",
      //手机号码的匹配
      iphone: /^1[3456789]\d{9}$/,
      //密码强度正则，最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
      //  code:/^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/,
      //校验密码：只能输入6-20个字母、数字、下划线
      //  code:/^(\w){6,20}$/,
      // /^(?=.*\d)(?=.*[a-zA-Z])(?=.*[~!@#$%^&*])[\da-zA-Z~!@#$%^&*]{8,}$/
      // 6-20位，包含数字和字母
      code: /^(?=.*\d)(?=.*[a-zA-Z])[\da-zA-Z]{6,20}$/,
      // 手机号
      phone: "",
      // 验证码
      captcha: "",
      // 是否点击了发送验证码
      sendFlag: false,
      // 验证码倒计时
      num: 60,
      // 短信key
      key: null,
      // 模式切换 1表示登录，2表示注册 3表示忘记密码
      mode: 1,
      // 注册页面的手机，验证码，密码
      sjs: "",
      yzs: "",
      mms: "",
      // 注册页面的验证码倒计时开关,true为倒计时
      yzmd: false,
      nums: 60,
      // 忘记密码
      zh_yzs: "",
      zh_sjs: "",
      zh_num: 60,
      zh_mms: "",
      // 忘记密码倒计时
      zh_f: false,
    };
  },
  created() {
    // 一进页面就获取key，用来防止恶意的短信验证码轰炸
    this.axios.get("/api/verify_code").then((res) => {
      this.key = res.data.data.key;
    });
  },
  methods: {
    // 账号登录
    doLogin(val) {
      this.$axios.post("api/login", val).then((res) => {
        // 登录失败
        if (res.data.status != 200) {
          this.$toast("您的账号或密码输入有误，请再次核对");
          return;
        }
        //登录成功
        // 设置token
        // token过期时间
        // 时间转换成时间戳
        let date = new Date(res.data.data.expires_time);
        let time = date.getTime();
        // console.log(time);
        this.$store.commit("setToken", res.data.data.token);
        // 设置cookie
        // document.cookie = `token=${res.data.data.token}`;
        this.$cookie.setCookie("token", res.data.data.token);
        // 把过期的时间戳存在cookie
        this.$cookie.setCookie("time", time);
        //设置用户信息
        this.$store.dispatch("getUser");
        // 跳转个人中心页面
        setTimeout(() => {
          this.$router.push("/mine");
        }, 500);
      });
    },
    // 手机号登录的验证
    logg(val) {
      this.axios.post("/api/login/mobile", val).then((res) => {
        // console.log(res);
        // 登录失败
        if (res.data.status != 200) {
          this.$toast("验证码错误，请先获取验证码");
        } else {
          let date = new Date(res.data.data.expires_time);
          let time = date.getTime();
          // console.log(time);
          this.$store.commit("setToken", res.data.data.token);
          // 设置cookie
          // document.cookie = `token=${res.data.data.token}`;
          this.$cookie.setCookie("token", res.data.data.token);
          // 把过期的时间戳存在cookie
          this.$cookie.setCookie("time", time);
          //设置用户信息
          this.$store.dispatch("getUser");

          this.$toast({
            type: "success",
            message: "登录成功",
          });
          // 跳转个人中心页面
          setTimeout(() => {
            this.$router.push("/mine");
          }, 500);
        }
      });
    },
    // 点击发送验证码
    //第一个参数是手机号，第二个参数是倒计时的参数，第三个是验证是否是手机号登录验证还是注册验证,true代表是注册验证
    send(p, f) {
      // 验证手机号码
      // f 1为登陆 2为注册 3为忘记密码
      // let types = f==1 ? "register" : "login";
      let types = f == 1 ? "login" : f == 2 ? "register" : "";
      if (/^1[3456789]\d{9}$/.test(p)) {
        this.axios
          .post("api/register/verify", {
            key: this.key,
            phone: p,
            type: types,
          })
          .then((res) => {
            console.log(res);
            //短信发送失败
            if (res.data.status != 200) {
              this.$toast(res.data.msg);
            } else {
              this.$toast("验证码发送成功!请输入验证码!");
            }
          });
      } else {
        this.$toast("手机号格式不正确");
        return;
      }
      // 手机验证成功发送短信倒计时
      if (f == 2) {
        // 注册
        this.yzmd = true;
      } else if (f == 1) {
        // 登录
        this.sendFlag = true;
      } else {
        // 忘记密码
        this.zh_f = true;
      }
      let timer = setInterval(() => {
        if (f == 2) {
          this.nums--;
        } else if (f == 1) {
          this.num--;
        } else {
          this.zh_num--;
        }
        let ms = f == 2 ? this.nums : f == 1 ? this.num : this.zh_num;
        if (ms <= 0) {
          clearInterval(timer);
          if (f == 2) {
            this.yzmd = false;
          } else if (f == 1) {
            this.sendFlag = false;
          } else {
            this.zh_f = false;
          }
          if (f == 2) {
            this.nums = 60;
          } else if (f == 1) {
            this.num = 60;
          } else {
            this.zh_num = 60;
          }
        }
      }, 1000);
    },
    // 验证不通过
    onFailed(errorInfo) {
      if (errorInfo.errors.length > 1) {
        this.$toast("您的账号和密码都输入有误，请再次核对");
      } else {
        if (errorInfo.errors[0].name == "password") {
          this.$toast("您的密码输入有误");
        } else {
          this.$toast("您的账号输入有误");
        }
      }
    },
    // 立即注册
    registerNow() {
      if (!/^(?=.*\d)(?=.*[a-zA-Z])[\da-zA-Z]{6,20}$/.test(this.mms)) {
        this.$toast("密码需要6-20位，并且包含数字和字母");
        return false;
      }
      if (!/^1[3456789]\d{9}$/.test(this.sjs)) {
        this.$toast("手机号输入有误");
        return false;
      }
      if (this.yzs == "") {
        this.$toast("验证码不能为空");
        return false;
      }
      this.axios
        .post("/api/register", {
          account: this.sjs,
          captcha: this.yzs,
          password: this.mms,
        })
        .then((res) => {
          // 注册失败
          if (res.data.status != 200) {
            this.$toast(res.data.msg);
          } else {
            this.$toast({
              type: "success",
              message: res.data.msg,
            });
            // 跳转到快捷登陆
            this.mode = true;
            this.active = 1;
            this.num = this.nums;
            this.sendFlag = true;
            this.phone = this.sjs;
            this.captcha = this.yzs;
            let timer = setInterval(() => {
              this.num--;
              if (this.nums < 0) {
                this.sendFlag = true;
                clearInterval(timer);
              }
            }, 1000);
          }
        });
    },
    // 忘记密码的确认键
    ack() {
      if (!/^(?=.*\d)(?=.*[a-zA-Z])[\da-zA-Z]{6,20}$/.test(this.zh_mms)) {
        this.$toast("密码需要6-20位，并且包含数字和字母");
        return false;
      }
      if (!/^1[3456789]\d{9}$/.test(this.zh_sjs)) {
        this.$toast("手机号输入有误");
        return false;
      }
      if (this.zh_yzs == "") {
        this.$toast("验证码不能为空");
        return false;
      }
      this.axios
        .post("/api/register/reset", {
          account: this.zh_sjs,
          captcha: this.zh_yzs,
          code: "",
          password: this.zh_mms,
        })
        .then((res) => {
          if (res.data.status != 200) {
            this.$toast(res.data.msg);
          } else {
            this.$toast({
              type: "success",
              message: res.data.msg,
            });
            this.mode =1;
          }
        });
    },
  },
};
</script>
<style lang="less">
#login {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  background: linear-gradient(to bottom, #ff4b1f, #ff9068);
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  .login_img {
    width: 100%;
    height: 2rem;
    box-sizing: border-box;
    background: url("http://47.115.51.185/h5/img/registerw.b3c17e1c.png")
      no-repeat 0 0/100% 100%;
    .van-image--round {
      box-sizing: border-box;
      background-color: #fff;
      padding: 0.06rem;
      img {
        width: 100%;
        height: auto;
        vertical-align: middle;
      }
    }
  }
  // 框框
  .kk {
    .lgoin_form {
      box-sizing: border-box;
      padding: 0.3rem 0;
      position: relative;
      .van-tabs__content {
        .van-tab__pane {
          .van-form {
            // 两个输入框
            .van-field {
              // 图标
              .van-field__left-icon {
                margin-right: 0.2rem;
              }
              // 框前文字
              .van-field__label {
                width: 0.8rem;
              }
              .van-field__value {
                .van-field__error-message {
                  font-size: 0.2rem;
                }
              }
            }
          }
        }
      }
      .zIn {
        position: relative;
        visibility: hidden;
      }
      .zIn2 {
        // visibility:visible;
        visibility: hidden;
      }
      // 忘记密码
      .forgotYourPassword {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        box-sizing: border-box;
        padding: 0 0.6rem;
        p {
          text-align: right;
          font-size: 0.28rem;
          color: #666;
          margin-top: 0.2rem;
          display: flex;
          align-items: center;
          i {
            font-size: 0.3rem;
            margin-right: 0.1rem;
            vertical-align: middle;
          }
        }
      }
      button {
        border: none;
        // background: linear-gradient(to bottom, #ee0979, #ff6a00);
        background: linear-gradient(to bottom, #ff4b1f, #ff9068);
        .van-button__text {
          position: relative;
          .btn-span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }
}
</style>
<style lang="less" scoped>
#login {
  .kk {
    margin: 0 auto;
    position: relative;
    background-color: #fff;
    border-radius: 0.2rem;
    .lgoin_form {
      width: 90vw;
      background-color: #fff;
      border-radius: 0.2rem;
      margin: 0 auto;
      // 顶部文字
      .van-tabs__wrap {
      }
      // 输入区域
      .van-tabs__content {
        .van-tab__pane {
          .van-form {
            // 两个输入框
            .van-field {
              // 框前文字
              .van-field__label {
                width: 4.2rem !important;
              }
            }
          }
        }
      }
    }
    // 注册
    .zc {
      text-align: center;
      font-size: 0.3rem;
      margin-bottom: 0.6rem;
      color: #ccc;
      span {
        color: #fc4141 !important;
      }
    }
    // 忘记密码
    .zh {
      height: 1.1rem;
      text-align: center;
      font-size: 0.3rem;
      color: #fc4141 !important;
    }
    // 注册框开始
    .signUp {
      width: 80vw;
      margin: 0 auto;
      box-sizing: border-box;
